body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  font-family: 'Verdana', cursive, sans-serif;
  color: #ffffff;
}

body {
  /* background-color: #7cc6fe; */
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.app {
  background-color: #252d4a;
  width: 500px;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}
.container {
  display: flex;
  flex-direction: column;
}
.score-section {
  display: flex;
  font-size: 24px;
  align-items: center;
}

/* QUESTION/TIMER/LEFT SECTION */
.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}

.timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  margin-top: 1rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

button {
  width: 90px;
  font-size: 16px;
  color: #ffffff;
  background-color: #299395;
  border-radius: 5px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid #2d7993;
  cursor: pointer;
  margin-right: 2rem;
}

.correct {
  background-color: #2f922f;
}

.incorrect {
  background-color: #ff3333;
}

button:hover {
  background-color: #555e7d;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}

input,
select,
option {
  color: black;
  padding: 10px;
  border: none;
  font-size: 16px;
  outline: none;
  border-radius: 5px;
  width: 100%;
}

.radio-container {
  display: flex;
  flex-direction: column;
  /* background-color: #ff3333; */
  justify-content: flex-start;
  align-items: flex-start;
  row-gap: 1rem;
}

.single-radio {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  /* background-color: #ff3333; */
  width: 100%;
}
.single-radio label {
  width: 300px;
  /* background-color: aqua; */
}

.result-container {
  display: flex;
  flex-direction: column;
  color: gray;
}
.result-container span {
  color: deeppink;
}
